  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
  height: 100%;
}

body {
  position: relative;
  display: grid;
  grid-template-rows: 1fr auto;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Poppins", "Arial", sans-serif;
  color: #1d1613;
}

.body-popup-background::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 200vh;
  top: 0;
  left: 0;
  background-color: #ffffff;
  z-index: 1;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 68px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0;
  border: 1px solid #a87b62;
  color: #ffffff;
  text-decoration: none;
  background-color: #a87b62;
  cursor: pointer;
}
.button:hover, .button:focus {
  opacity: 0.5;
}
.button:active {
  opacity: 0.3;
}

.page-header__wrapper {
  display: flex;
  justify-content: center;
  padding-left: calc(50% - 4px - 42vw);
  padding-right: calc(50% - 19px - 42vw);
  padding-top: 30px;
}

.page-header__site-search input {
  padding-top: 15px;
  padding-left: 12px;
  font-weight: 500;
  font-size: 14px;
  line-height: 15px;
  letter-spacing: 0.8px;
  color: #9b9997;
  background-color: transparent;
  border: none;
}

.page-header__logo {
  display: flex;
  margin-left: calc(50% - 234px);
  margin-right: auto;
}

.page-header--menu-style {
  background-color: #a87b62;
}

.page-header--menu-style svg {
  max-width: 66px;
  height: 66px;
  stroke: #ffffff;
  stroke-width: 1px;
}

.page-header__login {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.page-header__login li {
  margin-top: 9px;
  margin-left: 35px;
}

.page-header__login a {
  color: inherit;
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.4px;
  color: #1d1613;
}
.page-header__login a:hover, .page-header__login a:focus {
  opacity: 0.5;
}
.page-header__login a:active {
  opacity: 0.3;
}

.page-header__login svg {
  display: none;
}

.page-header__main-nav {
  margin-top: 20px;
  padding-left: 27px;
}

.page-header__site-menu {
  margin: 0;
  padding: 0;
  list-style: none;
