}

.page-header__slogan {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 463px;
  padding-top: 181px;
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  color: #ffffff;
  box-shadow: inset 0 0 0 240px rgba(86, 64, 52, 0.7);
  background-color: #a87b62;
  background-image: url("../img/main-necklace-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: calc(50% + 8px) 50%;
  background-size: cover;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
