.new-in {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 94px;
  margin-left: calc(50% - 4px - 42vw);
  margin-right: calc(50% - 21px - 42vw);
}

.new-in h2 {
  font-family: "Cormorant Garamond", "Arial", sans-serif;
  font-weight: 700;
  font-size: 34px;
  line-height: 37px;
}

.new-in > a {
  color: inherit;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  border-bottom: 1px solid #a87b62;
}
.new-in > a:hover, .new-in > a:focus {
  opacity: 0.5;
}
.new-in > a:active {
  opacity: 0.3;
}

.new-in__slider {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 60px;
  width: 100%;
  user-select: none;
  touch-action: pan-y;
}

.new-in__slider-list {
  overflow: hidden;
}

.new-in__slider-track {
  display: flex;
  width: 100%;
}

.new-in__slider-previous-button,
.new-in__slider-next-button {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: -115px;
  width: 29px;
  height: 29px;
  border: none;
  text-decoration: none;
  background-color: transparent;
  background-image: url("../img/icon-slider-arrow.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 100% 50%;
  cursor: pointer;
}
.new-in__slider-previous-button:hover, .new-in__slider-previous-button:focus,
.new-in__slider-next-button:hover,
.new-in__slider-next-button:focus {
  opacity: 0.5;
}
.new-in__slider-previous-button:active,
.new-in__slider-next-button:active {
  opacity: 0.3;
}

.new-in__slider-previous-button--off,
.new-in__slider-next-button--off {
  opacity: 0.2;
  pointer-events: none;
}
.new-in__slider-previous-button--off:hover, .new-in__slider-previous-button--off:focus,
.new-in__slider-next-button--off:hover,
.new-in__slider-next-button--off:focus {
  opacity: 0.2;
}
.new-in__slider-previous-button--off:active,
.new-in__slider-next-button--off:active {
  opacity: 0.2;
}

.new-in__slider-previous-button {
  left: 0;
}

.new-in__slider-next-button {
  right: 0;
  transform: rotate(180deg);
}

.new-in__slider-item {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-shrink: 0;
  justify-content: space-between;
  margin-left: 2.1%;
  width: 49%;
}

.new-in__slider-item:first-child {
  margin-left: 0;
}

.new-in__slider-controls {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 45px;
}

.new-in__slider-number-tablet {
  display: none;
}

.new-in__slider-number-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 7px;
  margin-left: 5px;
  padding: 0;
  font-family: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  border: none;
  text-decoration: none;
  background-color: transparent;
  color: #9b9997;
  cursor: pointer;
}
.new-in__slider-number-button:focus {
  opacity: 0.5;
}
.new-in__slider-number-button:active {
  opacity: 0.3;
}

.new-in__slider-number-button--active {
  color: #1d1613;
}

.new-in__slider-info {
  display: none;
}

